import React, { useState, useEffect, useRef } from "react";

function TodoForm(props) {
  const [input, setInput] = useState(props.edit ? props.edit.value : "");

  /*you want to change the value of an <input> element, 
but without using props or re-rendering the whole component.*/
  //const ref = useRef(initialValue)
  const inputRef = useRef(null);

  //focus on whatever is in the put as reference
  useEffect(() => {
    inputRef.current.focus();
  });

  const changeHandler = (e) => {
    setInput(e.target.value);
  };

  const submitHandler = (e) => {
    /* To prevent refreshing the site after click on submit*/
    e.preventDefault();

    props.onSubmit({
      /*give random id to todos*/
      id: Math.floor(Math.random() * 10000),
      text: input,
    });
    setInput("");
  };
  return (
    <form onSubmit={submitHandler} className="todo-form">
      {props.edit ? (
        <>
          <input
            placeholder="Update your item"
            value={input}
            onChange={changeHandler}
            name="text"
            ref={inputRef}
            className="todo-input edit"
          />
          <button onClick={submitHandler} className="todo-button edit">
            Update
          </button>
        </>
      ) : (
        <>
          <input
            placeholder="Add a todo"
            value={input}
            onChange={changeHandler}
            name="text"
            className="todo-input"
            ref={inputRef}
          />
          <button onClick={submitHandler} className="todo-button">
            Add todo
          </button>
        </>
      )}
    </form>
  );
}

export default TodoForm;
